.header_container {
  position: relative;

  .header_outside {
    z-index: 10;
    position: absolute;
    width: 100vw;
    height: calc(100vh + 660px);
    background-color: transparent;
    &.on {
      display: none;
    }
  }
  .header_text {
    position: absolute;
    width: 100%;
    h1 {
      z-index: 3;
      position: absolute;
      top: 0;
      left: 0;
      padding: 40px;
      font-size: 100px;
      font-family: 'League Gothic', sans-serif;
      color: #fff;
    }
    p {
      z-index: 3;
      position: absolute;
      top: 160px;
      left: 40px;
      font-size: 40px;
      font-family: 'League Gothic', sans-serif;
      color: #fff;
    }
  }
  .gnb {
    z-index: 30;
    position: fixed;
    top: -520px;
    left: 50%;
    width: 521px;
    height: 510px;
    background-color: #F059AB;
    // background-color: #fff;
    border-radius: 48px;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.3), inset 2px 2px 5px rgba(0,0,0,0.3);
    box-sizing: border-box;
    transform: translateX(-50%);
    transition: all 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    &.on {
      top: 50px;
      .hb_01 {
        opacity: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(-30deg);
      }
      .hb_02 {
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(30deg) !important;
      }
      .hb_03 {
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(-30deg);
      }
    }
    .gnb_container {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 70%;
      box-sizing: border-box;
      transform: translateX(-50%) translateY(-50%);
      // border: 1px solid red;
      .gnb_main {
        position: relative;
        box-sizing: border-box;
        font-family: 'League Gothic', sans-serif;
        color: #efefef;
        h2, h2>a {
          font-size: 36px;
          letter-spacing: 15px;
          padding: 10px 0;
          background-image: linear-gradient(to right, #F9ED69, #ABEDD8, #FFC4C4, #00e5ff, #FF165D, #892CDC, #DEEDF0);
          background-clip: text;
          -webkit-background-clip: text;
          color: transparent;
          text-decoration: underline;
          text-decoration-color: #fff;
          text-underline-offset: 8px;
        }
        .gnb_sub_box {
          overflow: hidden;
          box-sizing: border-box;
          .gnb_sub_container {
            height: 30px;
            box-sizing: border-box;
            transition: all .5s ease;
            &:hover {
              transform: translateY(-30px);
            }
            .gnb_sub {
              font-size: 24px;
              text-indent: 30px;
              line-height: 32px;
              &:nth-child(2) {
                font-family: 'Just Another Hand', cursive;
                // font-weight: 700;
                letter-spacing: -0.4px;
              }
            }
          }
        }
      }
    }
    .gnb_bottom {
      z-index: -1;
      position: absolute;
      bottom: -77px;
      left: 50%;
      width: 521px;
      height: 170px;
      background: url(../images/menu.svg) no-repeat 50% 50%;
      background-size: 100%;
      box-sizing: border-box;
      transform: translateX(-50%);
      content: '';
      transition: all 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      // border: 1px solid green;
      .header_btn_container {
        z-index: 100;
        position: absolute;
        bottom: 20px;
        left: 50%;
        width: 30px;
        height: 20px;
        box-sizing: border-box;
        // background-color: #fff;
        transform: translateX(-50%);
         transition: all 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        .header_btn {
          width: 30px;
          height: 2px;
          background-color: #111;
          transition: transform 1.2s ease-in 0s;
          cursor: pointer;
        }
        .hb_02 {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
        }
        .hb_03 {
          position: absolute;
          bottom: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 979px) {
  .header_text {
    h1 {
      font-size: 60px !important;
    }
    p {
      top: 100px !important;
      font-size: 30px !important;
    }
  }
}

@media screen and (max-width: 900px) {
  .gnb {
    width: 400px !important;
    height: 510px !important;
    .gnb_bottom {
      bottom: -55px !important;
      width: 400px !important;
      height: 132px !important;
      background-size: 400px !important;
      .header_btn_container {
        bottom: 15px !important;
        width: 25px !important;
        height: 14px !important;
        .header_btn {
          width: 25px !important;
        }
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .gnb {
    width: 300px !important;
    height: 506px !important;
    border-radius: 25px !important;
    .gnb_container {
      .gnb_main {
        h2 {
          font-size: 25px !important;
          letter-spacing: 15px;
          padding: 10px 0;
        }
        h2>a {
          font-size: 25px !important;
        }
        .gnb_sub {
          font-size: 20px !important;
          text-indent: 30px;
          line-height: 32px;
        }
      }
    }
    .gnb_bottom {
      width: 300px !important;
      height: 102px !important;
      background-size: 300px !important;
      .header_btn_container {
        bottom: 14px !important;
      }
    }
  }
}

// mobile version - landscape mode
.gnb-landscape-mode {
  top: -250px !important;
  // top: 40px !important;
  width: 260px !important;
  height: 250px !important;
  border-radius: 25px !important;
  &.on {
    top: 40px !important;
  }
  .gnb_container{
    height: 250px;
    // border: 1px solid yellow;
    .gnb_main {
      // border: 1px solid green;
      &:nth-child(1) {
        margin-top: 15px;
      }
      &:nth-child(1), &:nth-child(2) {
        margin-bottom: 20px;
      }
      h2, h2>a {
        position: absolute;
        background-image: linear-gradient(to bottom, #F9ED69, #ABEDD8, #FFC4C4, #00e5ff, #FF165D, #892CDC, #DEEDF0) !important;
        font-size: 18px !important;
        letter-spacing: 5px !important;
        writing-mode: vertical-lr;
        text-orientation:sideways;
        text-underline-offset: 3px !important;
        transform: translateX(-10px) rotate(180deg);
      }
      h2 {
        bottom: -20px !important;
      }
      &:nth-of-type(3) > h2 {
        bottom: -10px !important;
      }
      h2>a {
        transform: translate(-22px, 15px) rotateX(360deg);
      }
    .gnb_sub_box {
      height: 20px;
      // border: 1px solid red;
      .gnb_sub_container {
        // border: 1px solid green;
        &:hover {
          transform: translateY(-20px) !important;
        }
        .gnb_sub {
          font-size: 16px !important;
          line-height: 20px !important;
        }
      }
    }
    }
  }
  .gnb_bottom {
    bottom: -77px;
    width: 260px !important;
    height: 128px !important;
    background-size: contain !important;
    .header_btn_container {
      bottom: 30px !important;
    }
  }
}