.main_container{width:100vw;height:100vh;background-color:#9acd32;box-sizing:border-box;transition:transform .5s ease 0s}@keyframes main_fade{0%{opacity:0}100%{opacity:1}}.main_container .main_inner{position:relative;width:100vw;height:100vh;box-sizing:border-box;border:10px solid #000}.main_container .main_inner .main_scroll_up_btn{position:absolute;bottom:10px;left:50%;width:100px;height:100px;transform:translateX(-50%);animation:scroll_ani .5s linear 0s infinite alternate-reverse;cursor:pointer}@keyframes scroll_ani{0%{bottom:5px}100%{bottom:10px}}.main_container .main_inner .main_scroll_up_btn::before{position:absolute;left:50%;width:30px;height:50px;border-radius:20px;border:1px solid #fff;content:"";filter:blur(0.6px);transform:translateX(-50%)}.main_container .main_inner .main_scroll_up_btn::after{position:absolute;top:5px;left:50%;width:6px;height:12px;border-radius:3px;background-color:#fff;content:"";filter:blur(0.6px);transform:translateX(-50%)}.main_container .main_inner .main_scroll_up_btn span{position:absolute;bottom:20px;left:50%;width:100px;font-family:"League Gothic",sans-serif;font-size:18px;color:#fff;text-align:center;transform:translateX(-50%)}.main_container .main_inner .main_scroll_up_btn span::after{position:absolute;top:-85px;left:50%;content:"∧";filter:blur(0.5px);transform:translateX(-50%)}.main_container .main_inner .article_container{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;gap:30px;position:absolute;top:50%;left:50%;width:100%;transform:translateX(-50%) translateY(-50%)}.main_container .main_inner .article_container article{position:relative;width:300px;height:300px;box-sizing:border-box;border:10px solid #fff}.main_container .main_inner .article_container article h2{position:absolute;top:-30px;left:50%;padding:0 10px;background-color:#9acd32;font-family:"IBM Plex Sans KR",sans-serif;font-size:30px;color:#fff;transform:translateX(-50%)}.main_container .main_inner .article_container article span{position:absolute;font-size:80px;color:#fff}.main_container .main_inner .article_container article span.art_arrow{transform:rotate(90deg)}.main_container .main_inner .article_container article span.art_text{bottom:0;left:50%;font-family:"League Gothic",sans-serif}@media screen and (max-width: 980px){article{height:200px !important}}@media screen and (max-width: 649px){article{height:100px !important}article span{font-size:50px !important}article span.art_text{left:65% !important}}@media screen and (max-width: 896px)and (orientation: landscape){.main_container{height:100%}.article_container article{width:400px !important;height:120px !important}.article_container article span{font-size:60px !important}.article_container article span.art_arrow{left:1% !important}.article_container article span.art_text{left:70% !important}}@media screen and (max-height: 800px){.main_inner{min-height:710px !important}}